const LevelMarker = ({ level, isLocked }: { level: (typeof gameLevels)[0]; isLocked: boolean }) => (
  <div className="absolute group" style={{ ...level.position, transform: 'translateX(-50%)' }}>
    <Link href={isLocked ? '#' : `/game/level-${level.id}`} passHref>
      <div className={cn(
        "relative w-16 h-16 flex items-center justify-center rounded-full transition-all duration-300 transform group-hover:scale-110",
        isLocked ? 'bg-gray-500 cursor-not-allowed' : level.isHard ? 'bg-yellow-500 hover:bg-yellow-400' : 'bg-green-500 hover:bg-green-400',
        "shadow-lg border-4 border-white/50"
      )}>
        <div className="absolute inset-0 bg-black/20 rounded-full"></div>
        <div className="absolute top-1 w-12 h-8 bg-white/30 rounded-t-full"></div>
         {level.isHard ? (
          <Star className="relative w-8 h-8 text-white drop-shadow-md z-10" />
        ) : (
          <span className="relative text-white text-2xl font-bold drop-shadow-md z-10">{level.id}</span>
        )}
        {isLocked && <Lock className="absolute w-6 h-6 text-white z-10" />}
      </div>
    </Link>
    <div className="absolute left-1/2 -translate-x-1/2 mt-2 w-48 text-center p-2 bg-white/80 backdrop-blur-sm rounded-lg shadow-md opacity-0 group-hover:opacity-100 transition-opacity duration-300 pointer-events-none z-20">
      <p className="font-bold text-gray-800">{level.category}</p>
      <p className="text-sm text-gray-600">Level {level.id}: {level.isHard ? level.title : `Formula ${level.title}`}</p>
    </div>
  </div>
);


export default function GameHomePage() {
  usePageBackground('#dbeafe');
  const { user, getCompletedGameLevels } = useAuth();
  const [completedLevels, setCompletedLevels] = useState<number[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    if (user) {
      getCompletedGameLevels()
        .then(levels => {
          setCompletedLevels(levels);
          setIsLoading(false);
        });
    } else {
        setIsLoading(false);
    }
  }, [user, getCompletedGameLevels]);

  if (isLoading) {
    return <div className="text-center">
        <h1 className="text-5xl font-extrabold tracking-tight text-blue-600 font-headline drop-shadow-lg sm:text-6xl">Bubble Game Adventure</h1>
        <p className="mt-4 max-w-2xl mx-auto text-lg text-blue-800/80">
            Loading your adventure...
        </p>
        <div className="mt-8">
            <Skeleton className="h-[60vh] w-full max-w-lg mx-auto rounded-2xl" />
        </div>
    </div>
  }

  return (
    <div className="space-y-8">
      <div className="text-center">
        <h1 className="text-5xl font-extrabold tracking-tight text-blue-600 font-headline drop-shadow-lg sm:text-6xl">Bubble Game Adventure</h1>
        <p className="mt-4 max-w-2xl mx-auto text-lg text-blue-800/80">
          Pop your way through the levels to master your abacus skills!
        </p>
      </div>

       <ScrollArea className="h-[60vh] w-full max-w-lg mx-auto rounded-2xl shadow-2xl border-4 border-blue-300">
          <div className="relative w-full" style={{ height: '235%'}}>
             <Image
              src="https://placehold.co/800x2400/dbeafe/3b82f6?text=."
              alt="Vertical game map background"
              data-ai-hint="sky clouds"
              fill
              className="object-cover"
            />
            {/* Winding path SVG */}
            <svg className="absolute inset-0 w-full h-full" viewBox="0 0 200 1400">
              <path d="M 100,0 
                       C 50,50 150,100 100,150 
                       C 50,200 150,250 100,300 
                       C 50,350 150,400 100,450 
                       C 50,500 150,550 100,600 
                       C 50,650 150,700 100,750 
                       C 50,800 150,850 100,900 
                       C 50,950 150,1000 100,1050 
                       C 50,1100 150,1150 100,1200 
                       C 50,1250 150,1300 100,1350 
                       L 100,1400" 
                    stroke="#a3e635" strokeWidth="20" fill="none" strokeLinecap="round" strokeDasharray="1 40" />
            </svg>

            <div className="absolute inset-0">
               {gameLevels.map((level) => {
                  const isLocked = user ? level.id > 1 && !completedLevels.includes(level.id - 1) : level.id > 1;
                  return <LevelMarker key={level.id} level={level} isLocked={isLocked} />
               })}
            </div>
          </div>
       </ScrollArea>
    </div>
  );
}
